.radon{
 width: 100%;
    
}

 .radon__list{  
     
@include flex-direction(column);

float: left;
   
&:before{
 content: '';
 display: block;
 position: absolute;
 top: 0;
 bottom: auto;
 left: 0;
 border-left: 1px solid $keyline;
 height: 100%;
 border-top: 0px;
}


.radon__list-item{
    display: flex;
   position: relative;

    .promo{
        &:before {
    content: "";
    display: block;
    position: relative;
    top: -12px;
    bottom: auto;
    left: 0;
    width: calc(100%);
    border-top: 1px solid $keyline;
     @include mq(g5) {
    left: 0;
    width: calc(100%);
    }
}
        h3{ @extend %paragon, %paragon--bold;}
        
    }
    & ~ .radon__list-item{
          margin-top: $list-item-top-margin;
    }
        &:first-child{
            margin-top: none;
            .promo{
                
              &:before{
              content: none;
            }
            }
        
        
    
         }
  }
     
     
  
  }  
@include mq($from:g1, $until:g3) {
    .radon__list{
        width: 100%;
           &:before{
              content: '';
              display: block;
    position: relative;
    top: 0;
    bottom: auto;
    left: $g2-gutter-width / 2 ;
    border-top: 1px solid $keyline;
                  width: calc(100% - #{$g2-gutter-width});    
             }
    }
     .radon__list-item{
        margin-top: $list-item-top-margin / 2;
    }
}
@include mq($from:g3, $until:g5) {
    .radon__list{
       width: 100%;
     
  
         &:before{
              content: '';
              display: block;
    position: absolute;
    top: $g4-gutter-width / 2;
    bottom: auto;
    left: 0;
    border-left: 0;         
    border-top: 1px solid $keyline;
                width: calc(100% );    
             }
    }
     .radon__list-item{
        margin-top: $list-item-top-margin / 2;
    }
    
}

@include mq($from:g4, $until:g5) {
    .radon__list{
         &:before{
              content: '';
              display: block;
    position: absolute;
    top: 0;
    bottom: auto;
    left: 0;
    border-top: 1px solid $keyline;
                width: calc(100%);    
             }
    }
  

}

@include mq(g5) {
    .radon__list-item{
   display: flex;
    min-height: 70px;
        .promo__body{
        padding-bottom: 32px;
        }
        .metadata {
        position: absolute;
       bottom: 0;
      }
}
}
@include mq($from:g4, $until:g5) {

        .radon__list{  
    @include flex-flow(row wrap);
    display: flex;
           
    .radon__list-item{
     
        flex-basis: 33.333333333%;
       
            &:nth-child(-n+3){
                margin-top: $list-item-top-margin / 2;
       .promo{
             &:before{
              content: none;
             }
           }
    }
    }
        }
   
     }   


.radon__twoHeadlines{
    @include mq(g3) {
    
    .promo{
             @include promo-settings(
          /*PSEUDO CLASS (after/before) ----*/after,
/*DIVIDER CONTENT ----------------*/ '',
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   none,
        /*IMAGE WIDTH -------------------*/   100%,
        /*IMAGE PADDING -----------------*/   0px 0px 8px 0,
        /*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
        /*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
        /*METADATA PADDING --------------*/   0 0px,
        /*SEE ALSO PADDING --------------*/   8px 0 0px 0px
        );

    }
         }
    
    @include mq(g4) {
        .radon__list .radon__list-item{flex-basis: 50%;}
        .promo{
         @include promo-settings(
          /*PSEUDO CLASS (after/before) ----*/after,
/*DIVIDER CONTENT ----------------*/ none,
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   left,
        /*IMAGE WIDTH -------------------*/   30%,
        /*IMAGE PADDING -----------------*/   0px 16px 0px 0,
        /*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
        /*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
        /*METADATA PADDING --------------*/   0 0px,
        /*SEE ALSO PADDING --------------*/   8px 0 0px 0px
        );
         }
    }    
@include mq(g5) {
     
    .promo{
             @include promo-settings(
          /*PSEUDO CLASS (after/before) ----*/after,
/*DIVIDER CONTENT ----------------*/ '',
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   none,
        /*IMAGE WIDTH -------------------*/   100%,
        /*IMAGE PADDING -----------------*/   0px 0px 8px 0,
        /*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
        /*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
        /*METADATA PADDING --------------*/   0 0px,
        /*SEE ALSO PADDING --------------*/   8px 0 0px 0px
        );
        min-height: 260px;
    }
         }

}
