.promo{
	position: relative;
}
.promo:after{
    content:''!important;
    height: calc(100% + 12px);
    width: 100%;
    
    opacity: 1; //Start hidden, end hidden
  z-index:999;
  background-color:#fff;
    display: block;
    position: absolute; 
    top: 0;
}
.fade.promo:after {
opacity: 0;
transition: opacity 0.5s ease-in;
} 