.terbium{
overflow: hidden;   
@include organism__container;
	.terbium__container{
		display: table;
	}
.terbium__list {
      display: flex;
						flex-flow: row wrap;
     }  
	
	.second-column{
.terbium__list-item{
	margin-bottom: 16px;
			display: flex;
	position: relative;
	&:after {
    content: "";
    display: block;
    position: absolute;
    top: -12px;
    bottom: auto;
    left: 0;
    width: calc(100%);
    background-color: #F9F9F9;
    height: calc(100% + 24px);
    z-index: -999;
}
	.promo{

		position: relative;
		box-shadow: 0 2px 0px rgba(0,0,0,0.2);
    padding: 0 0 0 0;
		border-left: 1px solid #EAEAEA;
    border-right: 1px solid #eaeaea;
    background-color: #fff;
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;
	}	
	}
	}
	
	.terbium__list-item{margin-top:$list-item-top-margin; }	.terbium__content{
						display: flex;
					}  
    @include mq($from: g1, $until:g3){}//GROUP 1 & 2
    @include mq($from: g3, $until:g4){}//GROUP 3 
    @include mq($from: g4, $until:g5){
						.terbium__content{
						display: flex;
					}  
.terbium__list-item{ 

	@include flex-basis(33.333%); 
@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ none,
/*DIVIDER CONTENT ----------------*/ none,
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 8px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
/*PROMO BODY PADDING ------------*/   8px 0px 8px 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
					}
						.first-column{
						width: 66.666%;
						float: left;
					}
					.second-column{

						
						width: 33.333%;
						.terbium__list-item{ 
	@include flex-basis(100%); 
							@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ none,
/*DIVIDER CONTENT ----------------*/ none,
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 8px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   114.5%,
/*PROMO BODY PADDING ------------*/   8px 16px 8px 16px,
/*METADATA PADDING --------------*/   0 16px 8px 16px,
/*SEE ALSO PADDING --------------*/   8px 16px 0px 16px
);
						}
					}
					
	}//GROUP 4 
    @include mq(g5){

				      	.terbium__content{
						display: flex;
					}  
.terbium__list-item{ 
	@include flex-basis(33.333%); 
@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ none,
/*DIVIDER CONTENT ----------------*/ none,
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 8px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
/*PROMO BODY PADDING ------------*/   8px 0px 8px 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
}    
					
					.first-column{
						width: 56.25%;
						float: left;
					}
					.second-column{
						width: 43.75%;
						.terbium__list-item{ 
	@include flex-basis(100%); 
							@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ none,
/*DIVIDER CONTENT ----------------*/ none,
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 8px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   65%,
/*PROMO BODY PADDING ------------*/   8px 16px 8px 16px,
/*METADATA PADDING --------------*/   0 16px 8px 16px,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
						}
					}
    }//GROUP 5  
}