

.top-list__list{

width: 100%;
	.top-list-item{
	display: inline-block;
		position: relative;
			width: 100%;
margin-left: 16px;
			margin-top: 24px;

		@include mq($from: g4, $until:g5){
			.avatar{float: none;	width: 50%;margin: 0 auto 8px auto;}
			width: 20%;
			margin: 24px 0 0 0 ;
			    vertical-align: top;
			text-align: center;
			+ .top-list-item{
				padding-left: 16px;
			}
		}
	}
	.top-list-item__bullet{
		display: block;
	}
	.top-list-item__headline{
	
	
	}
	.top-list-item__byline{
		display: block;
font-style: italic;
font-family: "Georgia",Times,serif;
font-weight: normal;
margin-top: 0.25rem;
font-size: 0.875rem;
	}
	.avatar{
		width: 25%;
		float: left;
		margin-right: 16px;
		border-radius: 50%;
		.promo__image-square{
		background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
			padding-bottom: 100%;
					border-radius: 50%;
		}
	}
	
}
