
// @include horizontal__promo(right, 1.5, 8px);
// displays horizontal or vertical promo with image left or right or vertical
// it also controls the badge position



@mixin promo-settings(
  $promoDividerSelector, $promoDividerContent,
   $promo-padding,
   $image-float,
   $image__width,
   $image__padding,
   $image__height,
   $promo__body-padding,
   $metadata__padding,
   $see-also__padding
  ){
    .promo{
      @include sectionAfterDivider(
  /*PSEUDO CLASS (after/before) ----*/$promoDividerSelector,
  /*DIVIDER CONTENT ----------------*/ $promoDividerContent,
  /*TOP POSITION -------------------*/ -$list-item-top-margin / 2,
  /*BOTTOM POSITION ----------------*/ auto,
  /*LEFT POSITION ------------------*/ 0px,
  /*DIVIDER WIDTH ------------------*/ calc(100%)
  );
    }
    padding: $promo-padding;

    .promo__image-wrapper{
     position: relative;
     float: $image-float;
     width: $image__width;
     margin: $image__padding;
  }
   .promo__image-16x9{
     padding-bottom: $image__height;
     }

    .promo__body{

      padding: $promo__body-padding;
      }
     .metadata{
      display: table;
      padding: $metadata__padding;
     }
     .see-also{
      width: 100%;
      padding: $see-also__padding;
      margin-top: 0;
      display: inline-block;
      &:before{

      margin-bottom: 4px;
       }
      }
     .contributor{
      padding-bottom: 0;
     }
}

.promo {
@include flex-flow(column wrap);


   

    @include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/ '',
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   left,
/*IMAGE WIDTH -------------------*/   $g2-image-width-small,
/*IMAGE PADDING -----------------*/   0px 0px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
/*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);


  display: flex;
  flex: 1 1 auto;
  padding-left: 0;
  padding-right: 0;

  @include mq(g1) {
      .promo__container{position: relative;}      
  margin-left: $g2-gutter-width / 2;
  margin-right: $g2-gutter-width / 2;
      .contributor__avatar{display: none;}      
}

  @include mq(g3) {
       .promo__container{position:initial;}  
  margin-left: $g3-gutter-width / 2;
  margin-right: $g3-gutter-width / 2;
  .contributor__avatar{display: block;}       
}
  @include mq(g4) {
  margin-left: $g4-gutter-width / 2;
  margin-right: $g4-gutter-width / 2;
}
  @include mq(g5) {
  margin-left: $g5-gutter-width / 2;
  margin-right: $g5-gutter-width / 2;
}
   @include mq($from: 600px, $until:650px) {
     margin-left: $g2-gutter-width / 2;
  margin-right: $g2-gutter-width / 2;
  }
    
 @include mq($from: 360px, $until:599px) {
     .promo__content{
     .promo__image-wrapper{margin-bottom: 0}
     }
     .promo__image-wrapper + .promo__body {padding-bottom: 28px;}
     h3 + .contributor{display: block;
   
     }
     .promo__image-wrapper + .promo__body + .metadata {
         
         position: absolute;
         left: $g2-gutter-width / 2;
         margin-left: $g2-image-width-small;
         bottom: 0;
     }
  }    
.promo__container{
  width: 100%;
    .promo__content{
      box-sizing: border-box;
      display: inline-block;
      width: 100%;
      }
}

.image {
    display: block;
    margin-bottom: 8px;
  }

.promo__image-16x9{

    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
  }


  h3{ @extend %paragon, %paragon-bold;
  .promo__label{
       font-size: 12px;
      line-height: 14px;
      text-transform: uppercase;
      top: 0px;
        font-weight: 700;
    -webkit-font-smoothing: auto;
    &.headline{
      top: -2px;
    }
  }

  }

  h1{
    .promo__label{
      position: relative;
       font-size: 14px;
      line-height: 15px;
      top: -3px;
      letter-spacing: 0.5px;   
        margin-top: 12px;
    }
    @extend %trafalgar, %trafalgar-pica--bold;
   letter-spacing: -1px;
  }
  p {
    @extend %long-primer;
    color: $summary-copy;
    margin-top: 8px;
    margin-bottom: 0px;
    display: table;
    @include mq(g1) {display: none;}
    @include mq(g3) {display: inherit;}
    
        -webkit-font-smoothing: antialiased;  
  }

}
  .promo__label {

    z-index: 10;
  }

.promo.promo--standout{
  &:before{content: none;}
 @include mq($from:0px, $until:440px){

   @include promo-settings(
        /*PSEUDO CLASS (after/before) ---*/ before,
        /*DIVIDER CONTENT ---------------*/  none,
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   none,
        /*IMAGE WIDTH -------------------*/   100%,
        /*IMAGE PADDING -----------------*/   0px 0 0 0px,
        /*IMAGE HEIGHT (default 56.25%)--*/   80%,
        /*PROMO BODY PADDING ------------*/   0px $g2-gutter-width 8px $g2-gutter-width,
        /*METADATA PADDING --------------*/   0 $g2-gutter-width,
        /*SEE ALSO PADDING --------------*/   8px $g2-gutter-width 0px $g2-gutter-width
        );
  }
  @include mq($from:441px, $until:600px){

   @include promo-settings(
        /*PSEUDO CLASS (after/before) ---*/ before,
        /*DIVIDER CONTENT ---------------*/  none,
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   none,
