 
.carousel + .carbon{margin-top: 0;}
.carbon + .gold, .carbon + .aluminium, .carbon + .tin, .carbon + .terbium,.carousel + .terbium{
	.strapline{
	margin-top: 12px;
	&:before{content: none;}
	}
}
.carbon{
	margin-top: 24px;
	.strapline{
		&:before{content: none}
	margin-top: 8px;
	}
       .carbon__item{
           .promo__body{
        padding-bottom: 32px !important;
           }
    }
  &.standard{background-image:url("http://ichef.bbci.co.uk/images/ic/1600xn/p02b2fkf.jpg");}

@include organism__container__card;
position: relative;
background-color: #F9F9F9;
//background-image:url("http://ichef.bbci.co.uk/images/ic/1600xn/p02b2fkf.jpg");
background-size: cover;
background-position:  center;
&:before{
  content: '';
  display: block;
//       background: -webkit-linear-gradient(bottom, rgba(0,0,0,0.3) 0,rgba(0,0,0,0.2187) 10%,rgba(0,0,0,0.1536) 20%,rgba(0,0,0,0.1029) 30%,rgba(0,0,0,0.0648) 40%,rgba(0,0,0,0.0375) 50%,rgba(0,0,0,0.0192) 60%,rgba(0,0,0,0.0081) 70%,rgba(0,0,0,0.0024) 80%,rgba(0,0,0,0.0003) 90%,rgba(0,0,0,0) 100%);

    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    }


.strapline {
width: 100%;
color: #000;
@include mq(g1) {
margin-left: 6px; margin-right: 6px;
.strapline__container{padding: 8px 8px 8px 8px;}
}
@include mq(g3) {
margin-left: 16px;
margin-right: 16px;
.strapline__container{padding: 0px 0px 0px 0px;}
}
 p{ -webkit-font-smoothing: antialiased;}
}


  .carbon__content{
     position: relative;
    z-index: 10;
    display: flex;
    flex-direction: row;
    align-content: stretch;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: stretch;
    align-content: stretch;
}


.carbon__item{


  .promo{
    h3{@extend %paragon;}
      h3.double-pica{@extend %double-pica;}
    &.in-view{-webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;


  }


 animation-delay: 0.4s;
    opacity: .001;
    -webkit-transform: translateY(60px);
    transform: translateY(60px);
    transition: opacity .6s ease,transform .6s,ease;
}

position: relative;
display: flex;
width: 100%;
@include gutter($gutter-width);
@include mq(g1) {
@include horizontal__promo(left, 2, 8px);
margin-top: 8px;
.image{margin-bottom: 0;}
.metadata{display: inline-block;}
.promo__body{padding: 8px 8px 8px 8px; }
}
@include mq(g2) {
.topic-tags{padding-left: 8px;padding-right: 8px;}
  }
@include mq(g3) {
margin-top: 16px;
margin-bottom: 16px;
flex-basis: 50%;
  &:first-child{margin-bottom: 0;}
.topic-tags{padding-left: 16px;padding-right: 16px;}
@include horizontal__promo(none, 1, 16px);
&:before{right: 0; opacity: 0.4; content: none;}
.metadata{position: absolute; bottom:12px; left: 12px;}
.promo__body{ padding: 12px 16px 16px 16px; }
.promo__content{position: inherit;}
}

    @include mq(g4) {

      flex-basis: 33.333333333%;}
    @include mq(g5) {
      flex-basis: 25%;
      .topic-tags{padding-left: 0px;padding-right: 0px;}
  }


    &:last-child{
    &:before{content: none;}
      @include mq(g3) { flex-basis: 50%}
      @include mq(g4) { flex-basis: 100%}
        @include mq(g5) { flex-basis: 25%;}
      .promo{
      background-color: transparent;
      padding-bottom: 0;

        }
      }
    &:first-child{
    &:before{content: none;}
      @include mq(g1) { width: calc(4.166666667% * 24);}
      @include mq(g3) { flex-basis: 100%}
      @include mq(g4) { flex-basis: 100%;}
      @include mq(g5) { }
      }





  }

  .promo{background: #fff;
			box-shadow: 0 2px 0px rgba(0, 0, 0, 0.15);
			    border-left: 1px solid #EAEAEA;
    border-right: 1px solid #eaeaea;
     @include mq(g1) { padding-bottom: 0px;}
     @include mq(g3) { padding-bottom: 24px;}
			
    }

 
  &.hero{

    .carbon__item{
      .promo__body {
    padding: 8px 12px 16px 12px;
    }
        @include mq(g3) {
      &:nth-child(2){
        flex-basis: 100%;
        @include horizontal__promo(left, 2, 0px);
        .promo{ padding-bottom: 0px;}
        }
      &:nth-child(3), &:nth-child(4){
        flex-basis: 50%;
        }
           &:last-child{
        flex-basis: 100%;
        }
      }

        @include mq(g4) {
      &:nth-child(2){
        flex-basis: 44.4%;
        @include horizontal__promo(none, 1, 8px);
        }
      &:nth-child(3), &:nth-child(4){
        flex-basis: 27.8%;
        }

      }
      @include mq(g5) {
      &:nth-child(2){
        flex-basis: 37.4%;
        .promo__body {
    padding-top: 6px;
    }
         .promo{ padding-bottom: 24px;}
        }
      &:nth-child(3), &:nth-child(4){
        flex-basis: 18.8%;
        }
             &:last-child{
        flex-basis: 25%;
                
        }
      }


    }
  }

    &.hero__horizontal{

    .carbon__item{
      .promo__body {
    padding: 8px 12px 32px 12px;
    }
      @include mq(g1) {   &:last-child{
        .promo{background-color: #fff;}
        }}
        @include mq(g3) {
      &:nth-child(2){
        flex-basis: 100%;
        @include horizontal__promo(left, 2, 0px);
        .promo{ padding-bottom: 0px;}
        }
      &:nth-child(3), &:nth-child(4){
        flex-basis: 50%;
        }

      }

        @include mq(g4) {
      &:nth-child(2){
        flex-basis: 44.4%;
 @include horizontal__promo(none, 1, 0px);
        .promo{ padding-bottom: 24px;}
          .metadata {left: 12px;}
         .promo__body {
    padding-top: 6px;
             
    }
        }
      &:nth-child(3), &:nth-child(4){
        flex-basis: 22.2%;
            .metadata {left: 12px;}
        }
 &:last-child{
             
              flex-basis: 32%;
          }
      }
      @include mq(g5) {
      &:nth-child(2){
        flex-basis: 56.2%;
         @include horizontal__promo(right, 1.5, 0px);
 .promo{ padding-bottom: 0px;}
        .metadata {
    position: relative;
    padding-top: 16px;
            
}
        }
      &:nth-child(3), &:nth-child(4){
        flex-basis: 21.9%;
        
        }
          
          &:last-child{
             
              flex-basis: 21.9%;
          }

      }


    }
  }
}
